.navbar-expand-lg .navbar-nav .nav-link {
    font-size: medium;
    padding-left: 35px;
    color: #fff;
}

.navbar-expand-lg .navbar-collapse{height: 40px;}
.pic{height: 200px;}
.card{height: 300px; margin-top: 15px;}
.card-img-top{height: 150px; margin-left: 25%;}
.list{list-style: none;}
.list li{display: inline; padding-left: 20px;}

@media (max-width: 480px) 
{
    
    .container{width: 100%;}
    .top{height: 100px; }
    .logoo{max-width: 20%; height: 70px; margin-top: 10px;}
    .navbar-toggler{position: absolute; left: 80% !important; margin-bottom: 85px;}
    /* #navbarSupportedContent{
        background: red;
    } */
    .nav-link{background: #ccc; width: 450px; text-align: center;}
    .navbar-nav{position: absolute; left: 0%; z-index: 999;}

    .card{width: 250px; margin-left: 25%; background: whitesmoke; height: 250px;}
    .card-img-top{height: 100px; margin-left: 50px; margin-top: 20px; border: 1px solid #000; padding: 5px;}
    .list-group-item{background: #000; color: #fff;}
   

    

   

    
}

@media (max-width:786px) and (min-width:480px) {

    .container{width: 100%;}
    .top{height: 120px;}

    .logoo{max-width: 100px; height: 80px; position: absolute; margin-bottom: 10px;}
    .navbar-toggler{position: absolute; left: 90% !important; margin-bottom: 45px;}
    .nav-link{background: red; width: 120px; text-align: right;}
    .navbar-nav{position: absolute; top: 70px; left: 70%; z-index: 999;}
    .card{max-width: 400px;}
}